
















import {Component, Emit, Prop, Vue} from "vue-property-decorator";

@Component({})
export default class ModalDeletingConfirm extends Vue {
  @Prop({ required: true, type: Boolean }) show!: boolean
  @Prop({ required: true }) confirmHandler!: () => void

  onConfirm (): void {
    this.confirmHandler()
    this.close()
  }

  @Emit('close')
  close (): void {
    return
  }
}
